.app-calendar {
  color: #040809;
  width: 80%;
  margin: 0.4rem auto;
}
.app-calendar-title {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0.1rem 0;
}
.app-calendar-title .title-icon {
  flex-basis: 1rem;
  color: #040809;
  cursor: pointer;
}
.app-calendar-title .title-in {
  flex: 1 1;
  cursor: pointer;
}
.app-calendar-week {
  display: flex;
  height: 0.8rem;
  align-items: center;
  border-bottom: 1px solid rgba(4, 8, 9, 0.2);
}
.app-calendar-week > div {
  flex: 1 1;
  text-align: center;
}
.app-calendar-day {
  padding: 0.2rem 0;
  cursor: pointer;
}
.app-calendar-day .row {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
}
.app-calendar-day .col {
  flex: 1 1;
  min-width: 14.28571429%;
  max-width: 14.28571429%;
  line-height: 0.8rem;
  position: relative;
}
.app-calendar-day .col .active {
  background-color: #040809;
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 50%;
  display: inline-block;
  line-height: 0.4rem;
  text-align: center;
  color: #fff;
}
.app-calendar-day .disable {
  color: #cdcdcf;
}
.app-calendar-day .extra:after {
  content: '';
  position: absolute;
  width: 0.08rem;
  height: 0.08rem;
  background-color: #e71810;
  border-radius: 50%;
  bottom: 0;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  display: inline-block;
}
