._index {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

._index .head {
  position: relative;
  margin: 0 auto;
  background-color: #00b7e7;
  width: 180px;
  height: 155px;
  border-radius: 78px;
  border: 1px solid #666666;
}

._index .face {
  position: absolute;
  bottom: 10px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  background-color: #ffffff;
  width: 140px;
  height: 100px;
  border-radius: 70px;
}

._index .eye {
  position: absolute;
  width: 60%;
  top: -20px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  zoom: 1;
}

._index .eye:after {
  content: '';
  display: block;
  clear: both;
}

._index .eye .left_eye,
._index .eye .right_eye {
  position: relative;
  width: 46%;
  background-color: #ffffff;
  height: 45px;
  border-radius: 20px;
  border: 1px solid #666666;
  float: left;
}

._index .eye .left_eye_ball,
._index .eye .right_eye_ball {
  position: absolute;
  bottom: 18px;
  width: 7px;
  height: 7px;
  background-color: #000000;
  border-radius: 3.5px;
}

._index .eye .left_eye_ball {
  right: 5px;
}

._index .eye .right_eye_ball {
  left: 5px;
}

._index .nose {
  position: absolute;
  top: 20px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  width: 15px;
  height: 18px;
  border-radius: 10px;
  border: 1px solid #666666;
  background-color: red;
}

._index .nose_line {
  position: absolute;
  top: 39px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  width: 1px;
  height: 40px;
  background-color: #000000;
}

._index .mouse {
  position: absolute;
  top: 0px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  width: 80%;
  height: 80%;
  border-radius: 40%;
  border-bottom: 1px solid #000000;
}

._index .mustache-item {
  width: 100%;
  height: 1px;
  background-color: #666666;
  margin-bottom: 10px;
}

._index .mustache-left,
._index .mustache-right {
  position: absolute;
  top: 35px;
  width: 30px;
  height: 40px;
}

._index .mustache-left {
  left: 20px;
}

._index .mustache-left .mustache-item:first-child {
  transform: rotate(20deg);
  -webkit-transform: rotate(20deg);
}

._index .mustache-left .mustache-item:last-child {
  transform: rotate(-20deg);
  -webkit-transform: rotate(-20deg);
}

._index .mustache-right {
  right: 20px;
}

._index .mustache-right .mustache-item:first-child {
  transform: rotate(-20deg);
  -webkit-transform: rotate(-20deg);
}

._index .mustache-right .mustache-item:last-child {
  transform: rotate(20deg);
  -webkit-transform: rotate(20deg);
}

._index .neck {
  position: relative;
  margin: 0 auto;
  width: 120px;
  height: 10px;
  background-color: red;
  border: 1px solid #666666;
  border-radius: 5px;
}

._index .ring {
  position: absolute;
  top: 5px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  width: 20px;
  height: 20px;
  background-color: orange;
  border-radius: 10px;
  z-index: 55;
  border: 1px solid #666666;
}

._index .ring-line {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 100%;
  height: 2px;
  border: 1px solid #666666;
  border-radius: 1px;
}

._index .body {
  position: relative;
  margin: 0 auto;
  width: 150px;
  height: 100px;
  border: 1px solid #666666;
  border-radius: 20px;
  background-color: #00b7e7;
}

._index .hand-left,
._index .hand-right {
  z-index: 55;
  position: absolute;
  top: -40px;
  width: 40px;
  height: 100px;
}

._index .hand-left {
  left: 0;
  transform: rotate(30deg);
  -webkit-transform: rotate(30deg);
}

._index .hand-right {
  right: 0;
  transform: rotate(-30deg);
  -webkit-transform: rotate(-30deg);
}

._index .hand .hand-ball {
  position: absolute;
  top: 0;
  background-color: #ffffff;
  border: 1px solid #666666;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  z-index: 33;
}

._index .hand .hand-bounch {
  position: absolute;
  top: 20px;
  width: 40px;
  height: 60px;
  border-radius: 20px;
  background-color: #00b7e7;
  border: 1px solid #666666;
}

._index .foot-left,
._index .foot-right {
  z-index: 55;
  position: absolute;
  bottom: -20px;
  width: 60px;
  height: 80px;
  background-color: #ffffff;
  border: 1px solid #666666;
  border-radius: 40px;
}

._index .foot-left {
  left: -10px;
  transform: rotate(20deg);
  -webkit-transform: rotate(20deg);
}

._index .foot-right {
  right: -10px;
  transform: rotate(-20deg);
  -webkit-transform: rotate(-20deg);
}

._index .pocket {
  position: absolute;
  top: -5px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  width: 80%;
  height: 90%;
  background-color: #ffffff;
  border: 1px solid #666666;
  border-bottom-left-radius: 100px;
  border-bottom-right-radius: 100px;
}

._index .pocket-in {
  position: absolute;
  top: 35%;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  width: 70%;
  height: 40px;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
  border: 1px solid #666666;
}