.app-works-main {
  width: calc(80% - 0.8rem);
  margin: 0.5rem auto;
  display: flex;
  background-color: rgba(4, 8, 9, 0.02);
  padding: 0.4rem;
}
.app-works-main > div {
  flex: 1 1;
  position: relative;
}
.app-works-main .main-left {
  box-shadow: 1px 0 0 0 rgba(4, 8, 9, 0.2);
  line-height: 2;
  padding-right: 0.4rem;
}
.app-works-main .main-left .title {
  font-weight: bold;
}
.app-works-main .main-left .label {
  font-weight: bold;
}
.app-works-main .main-left pre {
  margin-bottom: 0;
}
.app-works-main .main-left code {
  font-size: 12px;
  line-height: 1;
}
.app-works-main .main-right {
  padding-left: 0.4rem;
}
@media screen and (max-width: 1400px) {
  div.app-works .app-works-main {
    flex-direction: column-reverse;
  }
  div.app-works .app-works-main .main-left {
    box-shadow: none;
    padding-right: 0;
  }
  div.app-works .app-works-main .main-right {
    padding-left: 0;
  }
  div.app-works .app-works-main .main-right > div {
    min-height: 6rem;
    width: 60%;
    margin: 0 auto;
  }
}
